	module.exports = {"data":{"markdownRemark":{"html":"<p>There is a very disappointing announcement about the distribution in this winter comic.</p>\n<h2>Winter comic distribution of TOUHOU UNREAL will be canceled</h2>\n<p>This is an honest story, it is too sorry that we call it a motion test version too, but it is very disappointing but we will not distribute the distribution in winter comics this time. Instead, as an apology, I will publish the operation test version completely free in January. After that, I will frequently frequently upgrade and publish the web, and I would like to publish the free trial version of the web to the web.</p>\n<h2>Delay of distribution of fate - Wind of peace -</h2>\n<p>Although it is a music CD that our circle has dealt with in consignment, this will be distributed from the afternoon with trouble on delivery. When you put together the talk, the distribution on the day</p>\n<ul>\n<li>\n<p><strong>All day distribution: Leila Prismriver (music CD: 300 yen)</strong></p>\n</li>\n<li>\n<p><strong>Distribution from the afternoon: Fate -Wind of peace- (Music CD / entrustment: 800 yen)</strong> Please note. Although it will be a very disappointing result for those who are looking for it from far away, I would like to apologize in this place. I appreciate your understanding.</p>\n<h2>Apology of banner typo</h2>\n<p>There was a typographical error in the banner of <a href=\"/!/leila/\">Leila Prismriver</a> . There is 11/30 C69, but correctly it is <strong>12/30 C69</strong> . (I think that there are no mistaken people to mistakenly) I corrected it, so I will apologize as I replace it.</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2005-12-28T23:30:00+09:00","strDate":"2005/12/28","title":"Announcement concerning the distribution of winter comic C 69"}}},"pathContext":{"path":"/en/blog/2005/12/28-冬コミc69の頒布物に関する告知/","langKey":"en"}}

